import React, {CSSProperties, useState} from 'react';
import './App.scss';
import guns from './parser/guns.json';

import logo from './assets/logo.svg';
import rifle from './assets/rifle.svg';
import medic from './assets/medic.svg';
import engineer from './assets/engineer.svg';
import sniper from './assets/sniper.svg';
import credit from './assets/credit.svg';
import vb from './assets/vb.svg';
import elit from './assets/elit.svg';
import gold from './assets/gold.svg';
import rm from './assets/rm.svg';
import craft from './assets/craft.svg';
import serial from './assets/serial.svg';
import holiday from './assets/holiday.svg';

interface ISetting {
    id: string;
    title: string;
    checked: boolean;
    icon: string;
    width: number;
}

interface IGun {
    title: string;
    image: string;
    class: string;
    type: string;
    subType: string;
    isGold: boolean;
    canRM: boolean;
}

// @ts-ignore
function putToCache(elem, cache){
    if(cache.indexOf(elem) !== -1){
        return;
    }
    var i = Math.floor(Math.random()*(cache.length + 1));
    cache.splice(i, 0, elem);
}

function madness(){
    // @ts-ignore
    var cache = [];
    // @ts-ignore
    return function(a, b){
        // @ts-ignore
        putToCache(a, cache);
        // @ts-ignore
        putToCache(b, cache);
        // @ts-ignore
        return cache.indexOf(b) - cache.indexOf(a);
    }
}

function shuffle(arr: IGun[]){
    var compare = madness();
    return arr.sort(compare);
}

const defaultClasses = ['rifleman', 'medic', 'engineer', 'sniper'];

function App() {
    const filterBySettings = (guns: IGun[], settings: ISetting[]): IGun[] => {
        const arr = guns.filter((gun: IGun) => {
            const byClass = settings.find((el: ISetting) => el.id === gun.class);
            const byGold = settings.find((el: ISetting) => el.id === 'gold');
            const byRM = settings.find((el: ISetting) => el.id === 'rm');
            const byCredits = settings.find((el: ISetting) => el.id === 'credit');
            const byElite = settings.find((el: ISetting) => el.id === 'elit');
            const bySerial = settings.find((el: ISetting) => el.id === 'serial');
            const byHoliday = settings.find((el: ISetting) => el.id === 'holiday');
            const byCraft = settings.find((el: ISetting) => el.id === 'craft');
            const byVb = settings.find((el: ISetting) => el.id === 'vb');

            if (typeof byClass !== 'undefined' && !byClass.checked) {
                return false;
            }

            if (typeof byGold !== 'undefined' && !byGold.checked && gun.isGold) {
                return false;
            }

            if (typeof byRM !== 'undefined' && byRM.checked && !gun.canRM) {
                return false;
            }

            if (typeof byCredits !== 'undefined' && !byCredits.checked &&
                (gun.type === 'credits' || gun.subType === 'globalEvent' || gun.subType === 'holiday')) {
                return false;
            }

            if (typeof byElite !== 'undefined' && !byElite.checked && gun.type === 'elite') {
                return false;
            }

            if (typeof bySerial !== 'undefined' && !bySerial.checked &&
                (gun.subType === 'serial' || gun.subType === 'other' || gun.subType === 'globalEvent' || gun.subType === 'operations')) {
                return false;
            }

            if (typeof byHoliday !== 'undefined' && !byHoliday.checked && gun.subType === 'holiday') {
                return false;
            }

            if (typeof byCraft !== 'undefined' && !byCraft.checked && gun.subType === 'crafting') {
                return false;
            }

            if (typeof byVb !== 'undefined' && !byVb.checked && gun.type !== 'credits') {
                return false;
            }

            return true;
        });
        return arr;
    };

    const [styleLeft, setStyleLeft]: [number, Function] = useState(0);
    const [withTransition, setTransition]: [boolean, Function] = useState(true);
    const [settings, setSettings]: [ISetting[], Function] = useState([
        {id: 'rifleman', title: 'Штурмовик', checked: true, icon: rifle, width: 90},
        {id: 'medic', title: 'Медик', checked: true, icon: medic, width: 90},
        {id: 'engineer', title: 'Инженер', checked: true, icon: engineer, width: 90},
        {id: 'sniper', title: 'Снайпер', checked: true, icon: sniper, width: 90},
        {id: 'credit', title: 'За кредиты', checked: true, icon: credit, width: 100},
        {id: 'vb', title: 'За варбаксы', checked: true, icon: vb, width: 100},
        {id: 'elit', title: 'Элитное', checked: false, icon: elit, width: 100},
        {id: 'gold', title: 'Золотое', checked: false, icon: gold, width: 100},
        {id: 'rm', title: 'Разрешено на РМ', checked: true, icon: rm, width: 140},
        {id: 'craft', title: 'Крафтовое', checked: false, icon: craft, width: 142},
        {id: 'serial', title: 'Из серии', checked: false, icon: serial, width: 142},
        {id: 'holiday', title: 'Праздничное', checked: false, icon: holiday, width: 142},
    ] as unknown as ISetting[]);
    const [gunsList, setGunsList]: [IGun[], Function] = useState(shuffle(filterBySettings(guns.data as IGun[], settings)));


    const changeChecked = (id: string) => {
        setTransition(false);
        const newSettings = [...settings];
        let countClassChecked = 0;
        let needUpdate = true;

        newSettings.forEach((el) => {
            if (defaultClasses.includes(el.id) && el.checked) {
                countClassChecked++;
            }
        });


        newSettings.forEach((el) => {
            if (el.id === id) {
                if (defaultClasses.includes(el.id) && countClassChecked === 1 && el.checked) {
                    needUpdate = false;
                    return;
                }
                el.checked = !el.checked;
            }
        });
        setSettings(newSettings);
        if (needUpdate) {
            setStyleLeft(0);
            setGunsList([...shuffle(filterBySettings(guns.data as IGun[], newSettings))]);
        }

        setTimeout(() => {
            setTransition(true);
        }, 100);
    };

    const random = () => {
        var random = Math.floor(Math.random() * gunsList.length);
        setStyleLeft(-random * 100);
        setTimeout(function() {
            random++;
        }, 5000)
    };

    return (
        <div className="wf-gun">
            <div className="wf-gun__header">
                <img src={logo} alt="warface random gun"/>
            </div>
            <div className="wf-gun__header-bg"> </div>
            <div className="wf-gun__content">
                <div className="wf-gun__content-settings">
                    { settings.map((setting) => (
                        <div key={setting.id} className="wf-gun__content-settings__item">
                            <img src={setting.icon} alt={setting.title}/>
                            <label htmlFor={setting.id} style={{width: setting.width}}>{setting.title}</label>
                            <input onChange={() => changeChecked(setting.id)} type="checkbox" id={setting.id} checked={setting.checked}/>
                        </div>
                    )) }
                </div>
                <div className="wf-gun__content-fortune">
                    <div className="wf-gun__content-fortune-shadow"> </div>
                    <div className="wf-gun__content-fortune-random">
                        <div className="wf-gun__content-fortune-result"> </div>
                        <div className={withTransition ? 'wf-gun__content-fortune-cards' : 'wf-gun__content-fortune-cards without-transition'} style={{left: styleLeft}}>
                            { gunsList.map((gun, idx) => (
                                <div key={idx}>
                                   <img src={gun.image} alt=""/>
                                   <span>{gun.title}</span>
                                </div>
                            )) }
                        </div>
                    </div>
                    <button className="wf-gun__content-fortune-start" onClick={random}>КРУТИТЬ</button>
                </div>
            </div>
        </div>
    );
}

export default App;
